import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TOEVol1Component } from './books/toeVol1.component';
import { TOEVol2Component } from './books/toeVol2.component';
import { TOEVol3Component } from './books/toeVol3.component';
import { OverviewComponent } from './overview/overview.component';
import { ContactComponent } from './contact/contact.component';
import { HeaderComponent } from './includes/header.component';

import { AppComponent } from './app.component';

let routes = [
  { path: "", component: OverviewComponent },
  { path: "overview", component: OverviewComponent },
  { path: "toeVol1", component: TOEVol1Component },
  { path: "toeVol2", component: TOEVol2Component },
  { path: "toeVol3", component: TOEVol3Component },
  { path: "contact", component: ContactComponent },
]

@NgModule({
  declarations: [
    AppComponent,
    TOEVol1Component,
    TOEVol2Component,
    TOEVol3Component,
    OverviewComponent,
    ContactComponent,
    HeaderComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes, {
      useHash: true,
      enableTracing: false //for debugging router
  }),
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
